  font-family: Poppins;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.light-theme-icon {
  margin-right: 13px;
}

.dark-theme-icon {
  margin-left: 13px;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #4b48db;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider {
  background-color: #4b48db;
}

input:checked + .slider:before {
  background-color: #fff;
}

input:focus + .slider {
  box-shadow: 0 0 1px #4b48db;
}

input:checked + .slider:before {
  transform: translateX(22px);
}

.slider.round {
  border: 1px solid #4b48db;
  border-radius: 20px;
}

.slider.round:before {
  border-radius: 50%;
}

.light-theme {
  color: #111321;
  background-color: #fff;
}

.dark-theme {
  color: #f4f4f4;
  background-color: #686868;
}

.dark-theme .logo {
  color: #fff;
}

.dark-theme .search-input {
  color: #f4f4f4;
  opacity: 1;
  background-color: #686868;
  border: 1px solid #fff;
}

.dark-theme .search-input::-ms-input-placeholder {
