}

.datepicker-controls .prev-button, .datepicker-controls .next-button {
  flex: 0 0 14.2857%;
  padding-left: .375rem;
  padding-right: .375rem;
}

.datepicker-controls .prev-button.disabled, .datepicker-controls .next-button.disabled {
  visibility: hidden;
}

.datepicker-view, .datepicker-grid {
  display: flex;
}

.datepicker-view {
  width: 15.75rem;
  align-items: stretch;
}

.datepicker-grid {
  flex-wrap: wrap;
  flex: auto;
}

.datepicker .days {
  flex-direction: column;
  flex: auto;
  display: flex;
}

.datepicker .days-of-week {
  display: flex;
}

.datepicker .week-numbers {
  flex-direction: column;
  flex: 0 0 9.67742%;
  display: flex;
}

.datepicker .weeks {
  flex-direction: column;
  flex: auto;
  align-items: stretch;
  display: flex;
}

.datepicker span {
  cursor: default;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.datepicker .dow {
  height: 1.5rem;
  font-size: .875rem;
  font-weight: 700;
}

.datepicker .week {
  color: #b8b8b8;
  flex: auto;
  font-size: .75rem;
}

.datepicker-cell, .datepicker .days .dow {
  flex-basis: 14.2857%;
}

.datepicker-cell {
  height: 2.25rem;
}

.datepicker-cell:not(.day) {
  height: 4.5rem;
  flex-basis: 25%;
}

.datepicker-cell:not(.disabled):hover {
  cursor: pointer;
  background-color: #f9f9f9;
}

.datepicker-cell.focused:not(.selected) {
  background-color: #e9e9e9;
}

.datepicker-cell.selected, .datepicker-cell.selected:hover {
  color: #fff;
  background-color: #3273dc;
  font-weight: 600;
}

.datepicker-cell.disabled {
  color: #dcdcdc;
}

.datepicker-cell.prev:not(.disabled), .datepicker-cell.next:not(.disabled) {
  color: #7a7a7a;
}

.datepicker-cell.prev.selected, .datepicker-cell.next.selected {
  color: #e6e6e6;
}

.datepicker-cell.highlighted:not(.selected):not(.range):not(.today) {
  background-color: #f5f5f5;
  border-radius: 0;
}

.datepicker-cell.highlighted:not(.selected):not(.range):not(.today):not(.disabled):hover {
  background-color: #efefef;
}

.datepicker-cell.highlighted:not(.selected):not(.range):not(.today).focused {
  background-color: #e9e9e9;
}

.datepicker-cell.today:not(.selected) {
  background-color: #00d1b2;
}

.datepicker-cell.today:not(.selected):not(.disabled) {
