}

.news-gallery {
  max-width: 320px;
  grid-template-columns: repeat(1, 1fr);
  margin-top: 20px;
  display: grid;
}

@media (min-width: 768px) {
  .news-gallery {
    width: 768px;
    max-width: 768px;
    flex-wrap: wrap;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}

@media (min-width: 1280px) {
